/* Reset CSS */

*{
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    vertical-align: baseline;
    text-decoration: none;
}

ul {
    list-style-type: none;
    }

/* Polices */


/* DÃ©claration de variables */

* {
    --color-black: #1B1B1B;
    --color-pink: #fb44ff;
    --color-blue: #00ffff;
    --color-white: white;
  }
  
html {
    scroll-behavior: smooth;
}

.App {   
    overflow-y: hidden;
    background: linear-gradient(0deg, rgba(27, 27, 27, 0.98), rgba(27, 27, 27, 0.85)), url(/my_portfolio/static/media/retro_back.ceb5e167.jpg);
    background-size: contain;
    background-attachment: fixed;
    width: 100vw;
}

main{
    width:90%;
}

.App h1, h2, h3, button {
    font-family: 'Press Start 2P', cursive;
    font-weight: bold;
}

.App a {
    font-size: 10px;
    font-family: 'Press Start 2P', cursive;
    font-style: italic;
    font-weight: lighter;
}

.App h1 {
    text-align: center;
    font-size: 30px;
    margin-top: 10vh;
    margin-bottom: 10vh;
    color: var(--color-white);
    text-shadow: 1px 1px var(--color-pink);
    width: 100%;
}

.App h2 {
    text-align: center;
    font-size: 18px;
    width: 100%;
}

.App h3 {
    text-align: center;
    font-size: 16px;
    margin-top: 2vh;
    margin-bottom: 2vh;
    text-align: center;
    width: 100%;
}

.App button {
    font-size: 14px;
}

.App p, span, li {
    font-family: 'VT323', monospace;
    font-size: 25px;
}

@media screen and (max-width: 1010px) {

    .App button {
        font-size: 10px;
    }
}
.home_container{
