.skills_container{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        min-height: 100vh;
        width: 100vw;
        border-bottom: solid 1px var(--color-blue);
}

.column_container{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    flex-wrap: wrap;
    width: 100%;
    height: 100%;
    margin-bottom: 100px;
}

.aside_container{
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: var(--color-white);
    height: 60vh;
    width: 20vw;
    padding: 40px;
    border-radius: 80px;
    border-bottom: solid 5px var(--color-pink);
    border-right: solid 2px var(--color-pink);
}

.logo_container{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    height: 100%;
}

.aside_container div img{
    height: 80px;
}

@media screen and (max-width: 1010px) {

    .skills_container h1{
        padding-bottom: 70px;
    }

    .aside_container{
        height: 40vh;
    }

    #nodejs{
        width:90%;
        height: auto;
    }

    .logo_container{
        height:80%;
        margin-top: 30px;
    }

    #vanilla{
        width: 95%;
        height:auto;
    }
}