.header_container{
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: fixed;
    z-index: 1;
    top: 0px;
    height: 15vh;
    width: 100%;
    transition: 0.4s ease-out;
    background: var(--color-black);
    box-shadow: 0px 0px 1px 1px var(--color-pink);
}

/* Taille du logo */

.header_container a {
    height: 100%;
}

/* Menu nav */

.header_container ul {
    display: flex;
    justify-content: space-around;
    align-items: center;
    max-width: 1500px;
    height: 100%;
}

.header_container ul li {  
    height: 30%;
    padding: 15px 30px 15px 30px;
}

.header_container ul li:hover {
    background-color: rgba(131, 131, 131, 0.1);
    border-bottom: solid var(--color-pink);
} 

.header_container ul li a {
    display: flex;
    justify-content: space-around;
    align-items: center;
    color: white;
    font-style: inherit;
    font-weight: inherit;
    font-size: 14px;
}

#logo_header {
    height: 100%;
}

.displayNone{
    display: none;
}


@media screen and (max-width: 1010px) {

    .header_container ul {
        width: 70%;
    }

    .header_container ul li {  
        height: 25%;
        padding: 5px 10px 5px 10px;
    }

    .header_container ul li a {
        font-size: 10px;
    }
}